const socialMedia = () => {
    let socialMedia = [
        {
            name: 'facebook',
            url: 'https://www.facebook.com/AlejandroMoralesEs',
            logo: 'fa-facebook'
        },
        {
            name: 'instagram',
            url: 'https://www.instagram.com/alejandrom0_0/',
            logo: 'fa-instagram'
        },
        {
            name: 'github',
            url: 'https://github.com/AlejandroMorales-s',
            logo: 'fa-github'
        },
    ]
    for (i in socialMedia) {
        let name =  socialMedia[i].name; 
        let url =  socialMedia[i].url; 
        let logo =  socialMedia[i].logo; 

        // Icon
        const icon = document.createElement('i');
        icon.classList.add('fab', '' + logo, 'social');

        // "a" social media
        const link = document.createElement('a');
        link.setAttribute('href', url);
        link.setAttribute('target', '_blank');
        link.appendChild(icon);

        document.getElementById('socialMedia').appendChild(link);
    }
}
socialMedia();